import React, { FC } from 'react';
import { graphql, useStaticQuery } from 'gatsby';
import Grid from '@material-ui/core/Grid';
import { default as ArticleCard } from '@src/components/ArticleCard';

type IProps = {
    limit?: number;
};
const NewestArticle: FC<IProps> = (props) => {
    const { limit } = props;
    const { allMdx } = useStaticQuery<Typing.Query>(graphql`
        query {
            allMdx(
                filter: { frontmatter: { published: { eq: true } } }
                sort: { order: DESC, fields: [frontmatter___date] }
            ) {
                edges {
                    node {
                        id
                        excerpt
                        frontmatter {
                            date(formatString: "YYYY/MM/DD")
                            title
                            tags
                            category
                            bannerPhoto {
                                childImageSharp {
                                    fluid {
                                        src
                                        originalName
                                    }
                                }
                            }
                            authorPhoto {
                                childImageSharp {
                                    fluid {
                                        src
                                        originalName
                                    }
                                }
                            }
                            author
                        }
                        fields {
                            slug
                        }
                    }
                }
            }
        }
    `);

    const data = allMdx.edges.slice(0, limit ? limit : allMdx.edges.length);

    return (
        <Grid container justify="center" spacing={3}>
            {data.map((edge) => (
                <Grid key={edge.node.id} item>
                    <ArticleCard articleData={edge.node} />
                </Grid>
            ))}
        </Grid>
    );
};

export default NewestArticle;
